      .work__linkIcon path {
        stroke: #fff;
        -webkit-transition: .4s;
        -o-transition: .4s;
        transition: .4s; }
    .work__link:hover {
      background: #fff; }
      .work__link:hover .work__linkIcon path {
        stroke: #000; }



